/** @jsx jsx */
import { graphql } from 'gatsby';
import { jsx, css } from '@emotion/core';
import { useMemo, useState, useEffect, useCallback } from 'react';

import useWindowWidth from '../hooks/useWindow';

import GroupByCategory from '../components/GroupByCategory';
import Layout from '../components/Layout';
import Utterances from '../components/Utterances';

import Menu from '../../contents/assets/menu.svg';

import 'prismjs/themes/prism-okaidia.css';

const articleWrapper = css`
  margin-left: 48px;
`;

const conttainer = css`
  display: grid;
  grid-template-columns: 280px 1fr;
  @media (max-width: 1024px) {
    grid-template-columns: 1fr;
  }
`;

const navWrapper = css`
  background-color: #f7f7f7;
  border-right-color: #cecece;
  border-right-style: solid;
  border-right-width: 1px;
  @media (max-width: 1024px) {
    border: none;
    position: absolute;
    width: 100%;
    height: 100%;
  }
`;

const subNavWrapper = css`
  border-bottom-color: #cecece;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  padding: 8px 20px;
  display: flex;
  align-items: ceter;
  line-height: 24px;
`;

const PostTemplate = ({
  data, // this prop will be injected by the GraphQL query below.
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [reactive, setReactive] = useState(false);

  const {
    allMarkdownRemark: { group },
    markdownRemark: {
      frontmatter: { title, category: categoryOfSelectedTitle, date },
      html,
    },
  } = data; // data.markdownRemark holds our post data

  const windowWidth = useWindowWidth();

  const categoryGroups: CategoryGroup[] = useMemo(() => {
    return group.map(g => {
      const { category, edges } = g;
      const postTitles = edges.map(edge => {
        const {
          node: {
            id,
            frontmatter: { title, path },
          },
        } = edge;
        return { id, title, path };
      });
      return {
        category,
        postTitles,
      };
    });
  }, [group]);

  useEffect(() => {
    if (windowWidth && windowWidth < 1024) setReactive(true);
    else setReactive(false);
  }, [windowWidth]);

  const handleMenuClick = useCallback(() => setIsOpen(!isOpen), [isOpen]);

  const navWrapperCombine = useMemo(
    () =>
      reactive && isOpen
        ? css`
            ${navWrapper} visibility:hidden;
          `
        : navWrapper,
    [isOpen, reactive]
  );

  return (
    <Layout>
      {reactive && (
        <div css={subNavWrapper}>
          <span
            css={css`
              height: 24px;
              width: 24px;
            `}
            onClick={handleMenuClick}
          >
            <Menu />
          </span>
          <span
            css={css`
              margin-left: 10px;
            `}
          >
            {categoryOfSelectedTitle}
          </span>
        </div>
      )}
      <div css={conttainer}>
        <nav css={navWrapperCombine}>
          <GroupByCategory
            categories={categoryGroups}
            categoryOfSelectedTitle={categoryOfSelectedTitle}
          />
        </nav>
        <article css={articleWrapper}>
          <div className="blog-post-container">
            <div className="blog-post">
              <h1>{title}</h1>
              <span>ðŸ—“{date}</span>
              <div
                className="blog-post-content"
                dangerouslySetInnerHTML={{ __html: html }}
              />
            </div>
            <Utterances />
          </div>
        </article>
      </div>
    </Layout>
  );
};

export default PostTemplate;

export const pageQuery = graphql`
  query($title: String!) {
    allMarkdownRemark {
      group(field: frontmatter___category) {
        category: fieldValue
        totalCount
        edges {
          node {
            id
            frontmatter {
              path
              title
            }
          }
        }
      }
    }
    markdownRemark(frontmatter: { path: { eq: $title } }) {
      html
      frontmatter {
        category
        date(formatString: "MMMM DD, YYYY")
        title
      }
    }
  }
`;
